import MintUI from 'mint-ui'

export default {
  getUrlKey: function (name) {
    return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(location.href) || [''])[1].replace(/\+/g, '%20')) || null
  },
  alert: function (msg) {
    MintUI.Toast({
      message: msg,
      position: 'middle',
      duration: 2000
    })
  },
  isWXUA: function () {
    var ua = navigator.userAgent.toLowerCase()
    if (/micromessenger/.test(ua)) {
      return true
    }
    return false
  }
}



