import api from '../api'

export default {
  login (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.post('api/user/login', data, successCallback, errCallback, isShowErrMsg)
  },
  userInfo (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.get('api/user/getUserInfo2', data, successCallback, errCallback, isShowErrMsg)
  }
}



